import React, { useEffect, useState } from 'react';
import { ActivityIndicator, FlatList, Text, View } from 'react-native';
import { Link } from "react-router-dom";

const ApiData = ()=>{

  const [ data, setData ] = useState([])

  useEffect(() => {
    fetch('https://makeup-api.herokuapp.com/api/v1/products.json?brand=maybelline')
    .then((res) => { return res.json() })
    .then((_data) => { setData (_data) })
    .catch((err) => { console.log(err)})
    .finally(() => { console.log('done')})
  },[])

  return(
    <>
    <br/>
            <h3 className="display-5">News Feed</h3>
<br/>
    <div className="container">
  <div  className="card">

    <div className="list-group">

      {
      data.map((action) =>   
       <a href={"/details/" + action.id } className="list-group-item list-group-item-action active" key={action.id} aria-current="true">

        {action.name} {action.price}
        <img src= {action.image_link} alt="BigCo Inc. logo"/>

        </a>
          

      )
    }
      
  
    
</div>
</div>
    </div>
    </>
  )
}
export default ApiData;