import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router';

const Details = () =>{

    const [ info, setInfo ] = useState([]);

    const { id : id } = useParams();
    const { brand : brand } = useParams();

   
    //get data by id77

    useEffect(() =>{
        fetch("https://makeup-api.herokuapp.com/api/v1/products.json?brand=maybelline/" + id ) 
        .then((res) => { 
        return res.json()
    })
        .then((data) => {
             setInfo(data);
             });
    });
    return(
        <>
        {/*this is for displaying the data from the previous page*/}

        <div className="container-fluid mt-4">

        <div className="card">
  <div className="card-header">
    Featured id:  {id}

  </div>
  <div className="card-body">
  
    <h5 className="card-title"><p>Hello{info.name}</p>{info.name}</h5>
    <p className="card-text">{info.brand}</p>
    { JSON.stringify(brand)}
    
    <a href="#" className="btn btn-primary">Go somewhere</a>
  </div>
</div>
</div>
        </>
    
    )
    
}

export default Details